/*
  -------------------------------------------------------
   Imports:
  -------------------------------------------------------
*/
import sortIconAsc from "assets/img/Ascending_Sort.svg";
import sortIconsDesc from "assets/img/Descending_Sort.svg";
import LoadingIcon from "assets/img/common/Loading.svg";
import eyeIcon from "assets/img/common/eye_icon.svg";
import sortIcon from "assets/img/sortIcon.svg";
import AutoComplete from "components/AutoComplete";
import ButtonBox from "components/ButtonBox";
import DatePicker from "components/DatePicker";
import DialogBox from "components/Dialog/Dialog";
import InputBox from "components/InputBox";
import MenuBox from "components/MenuBox/MenuBox";
import ReadOnlyTable from "components/ReadOnlyTable";
import SectionTitle from "components/SectionTitle";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text";
import ToastCustom from "components/Toast";
import { RouteProps, withRouter } from "hoc/withRouter";
import { CalendarChangeParams } from "primereact/calendar";
import { Column } from "primereact/column";
import { ChangeEvent, useEffect, useRef, useState } from "react";
import { linkStateCreateViewEdit } from "routes";
import StateService from "services/masterdata/Location/State";
import UserService from "services/masterdata/User/UserService";
import {
  convertDateToISO,
  formattedDateLong,
  parseStandardAPIErrorMessage,
  stringifyObject,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  ENTITY,
  INPUT_STYLE_TYPE,
  IS_ACTIVE,
  OPERATION_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";

/*
  -------------------------------------------------------
  PROPS:
  -------------------------------------------------------
*/
type IProps = RouteProps

function PageState(props: IProps) {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */

  const [showFilter, setShowFilter] = useState(false);
  const [showDialogDeactivate, setShowDialogDeactivate] = useState(false);
  const [showDialogActivate, setShowDialogActivate] = useState(false);
  const [selectedData, setSelectedData] = useState<any>({});
  const [displayNameAsc, setDisplayNameAsc] = useState<any>(undefined);
  const [codeAsc, setCodeAsc] = useState<any>(undefined);
  const [descriptionAsc, setDescriptionAsc] = useState<any>(undefined);
  const [createdAtAsc, setCreatedAtAsc] = useState<any>(undefined);
  const [createdByAsc, setCreatedByAsc] = useState<any>(undefined);
  const [updatedAtAsc, setUpdatedAtAsc] = useState<any>(undefined);
  const [updatedByAsc, setUpdatedByAsc] = useState<any>(undefined);
  const [isActiveAtAsc, setIsActiveAsc] = useState<any>(undefined);
  const toastMessageRef = useRef(null);
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [isActivateClicked, setIsActivateClicked] = useState(false);
  const [searchObj, setSearchObj] = useState({
    staticMasterType: {
      name: "",
    },
    name: "",
    code: "",
    description: "",
    createdFrom: "",
    createdTo: "",
    updatedFrom: "",
    updatedTo: "",
    isActive: "",
    createdBy: {
      name: "",
      id: "",
    },
    updatedBy: {
      name: "",
      id: "",
    },
  });

  const [dataTable, setDataTable] = useState({
    limit: 10,
    loading: false,
    totalRecords: 0,
    start: 0,
    search: "",
    runCustomQuery: false,
    records: [],
  });
  const activeRowRef = useRef(null);
  const [editModuleCardMenu, setEditModuleCardMenu] = useState<any>([
    {
      items: [
        {
          label: "Activate",
          className: "",
        },
        {
          label: "Deactivate",
          className: "",
        },
      ],
    },
  ]);
  const onToggleMenu = (rowData: any, e: any) => {
    console.log("rowData=", rowData);

    const isActive = {
      label: "label.common.deactivate",
      command: () => {
        deactivateRow(rowData);
      },
    };
    const isNotActive = {
      label: "label.common.active",
      command: () => {
        activateRow(rowData);
      },
    };

    const item = rowData?.isActive ? isActive : isNotActive;
    const list = [
      {
        items: [item],
      },
    ];
    setEditModuleCardMenu(list);

    // @ts-ignore
    activeRowRef?.current?.toggle(e);
  };
  const [createdByTemplate, setCreatedByTemplate] = useState<
    {
      name?: string | undefined;
      designation?: string | undefined;
      emailId?: string | undefined;
    }[]
  >([]);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<
    {
      name?: string | undefined;
      // emailId?: string | undefined;
    }[]
  >();

  const dataTableRef = useRef(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */

  useEffect(() => {
    // @ts-ignore
    dataTableRef?.current?.search();
  }, [dataTable?.search]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */

  const handleAutoCompleteFilterChangeDisplayName = (event: any) => {
    const { name, value } = event.target;
    if (typeof value === "object") {
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: value.name,
      }));
    } else
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: value,
      }));
  };

  const handleAutoCompleteFilterChange = (event: any) => {
    const { name, value } = event.target;
    if (typeof value === "object") {
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: {
          name: `${value?.name} ${value?.lastName ? value?.lastName : ""}`.trim(),
          id: value.userId,
        },
      }));
    } else
      setSearchObj((prevFilterObj) => ({
        ...prevFilterObj,
        [name]: value,
      }));
  };

  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | CalendarChangeParams) => {
    const { name, value } = event.target;

    setSearchObj((prevFilterObj) => ({
      ...prevFilterObj,
      [name]: value,
    }));
  };

  const sortDisplayName = (event: any) => {
    let search = "";
    if (displayNameAsc || displayNameAsc == undefined) {
      search = `sort={"name":"ASC"}&`;
    } else {
      search = `sort={"name":"DESC"}&`;
    }

    if (displayNameAsc == undefined) {
      setDisplayNameAsc(false);
    } else {
      setDisplayNameAsc(!displayNameAsc);
    }
    setCodeAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);
    setDescriptionAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const activateRow = (rowData: any) => {
    setSelectedData({ ...rowData });
    setShowDialogActivate(true);
    setIsActivateClicked(true);
  };

  const deactivateRow = (rowData: any) => {
    setSelectedData({ ...rowData });
    setShowDialogDeactivate(true);
    setIsActivateClicked(false);
  };

  const sortSystemCode = (event: any) => {
    let search = "";
    if (codeAsc || codeAsc == undefined) {
      search = `sort={"code":"ASC"}&`;
    } else {
      search = `sort={"code":"DESC"}&`;
    }

    if (codeAsc == undefined) {
      setCodeAsc(false);
    } else {
      setCodeAsc(!codeAsc);
    }

    setDataTable({
      ...dataTable,
      search,
    });
    setDisplayNameAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);
  };

  const sortDescription = (event: any) => {
    let search = "";
    if (descriptionAsc || descriptionAsc == undefined) {
      search = `sort={"description":"ASC"}&`;
    } else {
      search = `description={"name":"DESC"}&`;
    }

    if (descriptionAsc == undefined) {
      setDescriptionAsc(false);
    } else {
      setDescriptionAsc(!descriptionAsc);
    }

    setDataTable({
      ...dataTable,
      search,
    });
    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);
  };

  const sortCreatedOn = (event: any) => {
    let search = "";
    if (createdAtAsc || createdAtAsc == undefined) {
      search = `sort={"createdAt":"ASC"}&`;
    } else {
      search = `sort={"createdAt":"DESC"}&`;
    }

    if (createdAtAsc == undefined) {
      setCreatedAtAsc(false);
    } else {
      setCreatedAtAsc(!createdAtAsc);
    }

    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortCreatedBy = (event: any) => {
    let search = "";
    if (createdByAsc || createdByAsc == undefined) {
      search = `sort={"createdBy":"ASC"}&`;
    } else {
      search = `sort={"createdBy":"DESC"}&`;
    }

    if (createdByAsc == undefined) {
      setCreatedByAsc(false);
    } else {
      setCreatedAtAsc(!createdAtAsc);
    }

    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedAtAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortUpdatedOn = (event: any) => {
    let search = "";
    if (updatedAtAsc || updatedAtAsc == undefined) {
      search = `sort={"updatedAt":"ASC"}&`;
    } else {
      search = `sort={"updatedAt":"DESC"}&`;
    }

    if (updatedAtAsc == undefined) {
      setUpdatedAtAsc(false);
    } else {
      setUpdatedAtAsc(!updatedAtAsc);
    }

    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedAtAsc(undefined);
    setIsActiveAsc(undefined);
    setCreatedByAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortIsActive = (event: any) => {
    let search = "";
    if (isActiveAtAsc || isActiveAtAsc == undefined) {
      search = `sort={"isActive":"ASC"}&`;
    } else {
      search = `sort={"isActive":"DESC"}&`;
    }

    if (isActiveAtAsc == undefined) {
      setIsActiveAsc(false);
    } else {
      setIsActiveAsc(true);
    }

    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedAtAsc(undefined);
    setUpdatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortUpdatedBy = (event: any) => {
    let search = "";
    if (updatedByAsc || updatedByAsc == undefined) {
      search = `sort={"updatedBy":"ASC"}&`;
    } else {
      search = `sort={"updatedBy":"DESC"}&`;
    }

    if (updatedByAsc == undefined) {
      setUpdatedByAsc(false);
    } else {
      setUpdatedByAsc(!updatedByAsc);
    }

    setDisplayNameAsc(undefined);
    setCodeAsc(undefined);
    setDescriptionAsc(undefined);
    setCreatedAtAsc(undefined);
    setUpdatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const onSaveDeactivate = () => {
    return StateService.Inactivate(selectedData?.uid)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.DEACTIVATED);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onSaveActivate = () => {
    return StateService.activate(selectedData?.uid)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ACTIVATED);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onApproveActivate = () => {
    const initialJsonObject = {
      staticMasterType: selectedData?.staticMasterType?.name,
      name: selectedData?.name,
      code: selectedData.code,
      reason: selectedData.reason,
      description: selectedData.description,
      createdAt: selectedData.createdAt,
      createdBy: selectedData.createdBy,
      id: selectedData.id,
      isActive: false,
      requestId: selectedData.requestId,
      uid: selectedData.uid,
      updatedAt: selectedData.updatedAt,
      updatedBy: selectedData.updatedBy,
    };
    const finalJsonObject = {
      ...initialJsonObject,
      isActive: true,
    };
    const enrichedObj = {
      name: selectedData.displayName,
      operationType: OPERATION_TYPE.UPDATE,
      entity: ENTITY.STATIC_MASTER_DATA,
      initialStateJson: stringifyObject(initialJsonObject),
      finalStateJson: stringifyObject(finalJsonObject),
      reasonForChange: selectedData.reasons,
    };
    return StateService.update(selectedData?.uid, enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onApproveDeActivate = () => {
    const initialJsonObject = {
      staticMasterType: selectedData?.staticMasterType?.name,
      name: selectedData?.name,
      code: selectedData.code,
      reason: selectedData.reason,
      description: selectedData.description,
      createdAt: selectedData.createdAt,
      createdBy: selectedData.createdBy,
      id: selectedData.id,
      isActive: true,
      requestId: selectedData.requestId,
      uid: selectedData.uid,
      updatedAt: selectedData.updatedAt,
      updatedBy: selectedData.updatedBy,
    };
    const finalJsonObject = {
      ...initialJsonObject,
      isActive: false,
    };
    const enrichedObj = {
      name: selectedData.displayName,
      operationType: OPERATION_TYPE.UPDATE,
      entity: ENTITY.STATIC_MASTER_DATA,
      initialStateJson: stringifyObject(initialJsonObject),
      finalStateJson: stringifyObject(finalJsonObject),
      reasonForChange: selectedData.reasons,
    };
    return StateService.update(selectedData?.uid, enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */

  const searchAutoCompleteDisplayName = async (event: any) => {
    let suggestionsTemplate = [];
    if (!event.query.trim().length) {
      suggestionsTemplate = createdByTemplate;
    } else {
      await StateService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
        if (res.status === 200) {
          suggestionsTemplate = [...res.data.results];
          // setIsLoading(false);
        }
      });
      if (suggestionsTemplate.length === 0) {
        suggestionsTemplate.push({ name: undefined, designation: undefined, emailId: undefined });
      }
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCreatedBy = async (event: any) => {
    let suggestionsTemplate = [];
    if (!event.query.trim().length) {
      suggestionsTemplate = createdByTemplate;
    } else {
      await UserService.getAll(0, 10, `name=${event.query}&`).then((res) => {
        if (res.status === 200) {
          suggestionsTemplate = [...res.data];
          // setIsLoading(false);
        }
      });
      if (suggestionsTemplate.length === 0) {
        suggestionsTemplate.push({ name: undefined, designation: undefined, emailId: undefined });
      }
    }
    setCreatedByTemplate(suggestionsTemplate);
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */
  const onSearch = () => {
    let search = "";
    if (searchObj?.staticMasterType?.name) {
      search = `${search}staticMasterType.name:lk=${searchObj?.staticMasterType?.name}&`;
    }
    if (searchObj.name) {
      search = `${search}name:lk=${searchObj.name}&`;
    }
    if (searchObj.code) {
      search = `${search}code:lk=${searchObj.code}&`;
    }
    if (searchObj.description) {
      search = `${search}description:lk=${searchObj.description}&`;
    }
    if (searchObj.createdFrom) {
      console.log(searchObj.createdFrom);
      search = `${search}createdAt:gt=${convertDateToISO(searchObj.createdFrom)}&`;
    }
    if (searchObj.createdTo) {
      search = `${search}createdAt:lt=${convertDateToISO(searchObj.createdTo)}&`;
    }
    if (searchObj.updatedFrom) {
      search = `${search}updatedAt:gt=${convertDateToISO(searchObj.updatedFrom)}&`;
    }
    if (searchObj.updatedTo) {
      search = `${search}updatedAt:lt=${convertDateToISO(searchObj.updatedTo)}&`;
    }
    if (searchObj.isActive) {
      let isActiveBool = true;
      if (searchObj.isActive === "YES") {
        isActiveBool = true;
      } else if (searchObj.isActive === "NO") {
        isActiveBool = false;
      }
      search = `${search}isActive:lk=${isActiveBool}&`;
    }
    if (searchObj.createdBy?.id) {
      search = `${search}&createdBy=${searchObj.createdBy?.id}&`;
    }
    if (searchObj.updatedBy?.id) {
      search = `${search}&updatedBy=${searchObj.updatedBy?.id}&`;
    }
    setDataTable({
      ...dataTable,
      search,
    });
  };
  const onReset = () => {
    setSearchObj({
      staticMasterType: {
        name: "",
      },
      name: "",
      code: "",
      description: "",
      createdFrom: "",
      createdTo: "",
      updatedFrom: "",
      updatedTo: "",
      isActive: "",
      createdBy: {
        name: "",
        id: "",
      },
      updatedBy: {
        name: "",
        id: "",
      },
    });
    setDataTable({
      ...dataTable,
      search: "",
    });
  };
  const onToggleFilter = () => {
    setShowFilter(!showFilter);
  };
  const onCreate = () => {
    props.navigate(`${linkStateCreateViewEdit}?mode=${PAGE_MODE.CREATE}`);
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */

  const renderFilter = () => {
    const {
      name,
      code,
      description,
      createdFrom,
      createdTo,
      updatedFrom,
      updatedTo,
      isActive,
      createdBy,
      updatedBy,
    } = searchObj;
    return (
      <>
        <div className="p-3 flex flex-col gap-6 bg-primary-sapphireBlue-100 rounded-lg">
          <div className="flex ">
            <SectionTitle title="text.masterData.types.search" />
          </div>
          <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
            <AutoComplete
              label="input.state.displayName"
              name="name"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={name}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteDisplayName}
              onChange={handleAutoCompleteFilterChangeDisplayName}
              itemTemplate={itemDisplayNameTemplate}
            />

            <InputBox
              label="input.state.description"
              name="description"
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleFilterChange}
              value={description}
            />
            <SimpleDropDown
              label="input.state.isActive"
              name="isActive"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              options={IS_ACTIVE}
              onChange={handleFilterChange}
              value={isActive}
            />
            <DatePicker
              label="input.state.createdFrom"
              name="createdFrom"
              showTime
              value={createdFrom}
              placeholder="MM/DD/YYYY"
              onChange={handleFilterChange}
            />

            <DatePicker
              showTime
              label="input.state.createdTo"
              name="createdTo"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={createdTo}
              placeholder="MM/DD/YYYY"
              onChange={handleFilterChange}
            />

            <AutoComplete
              label="input.state.createdBy"
              name="createdBy"
              field="name"
              value={createdBy}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={createdByTemplate}
              completeMethod={searchAutoCompleteCreatedBy}
              onChange={handleAutoCompleteFilterChange}
              itemTemplate={itemTemplate}
            />

            <DatePicker
              showTime
              label="input.state.updatedFrom"
              name="updatedFrom"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={updatedFrom}
              placeholder="MM/DD/YYYY"
              onChange={handleFilterChange}
            />

            <DatePicker
              showTime
              label="input.state.updatedTo"
              name="updatedTo"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={updatedTo}
              placeholder="MM/DD/YYYY"
              onChange={handleFilterChange}
            />

            <AutoComplete
              field="name"
              label="input.state.updatedBy"
              name="updatedBy"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={updatedBy}
              suggestions={createdByTemplate}
              completeMethod={searchAutoCompleteCreatedBy}
              onChange={handleAutoCompleteFilterChange}
              itemTemplate={itemTemplate}
            />

            <div className="flex gap-5">
              <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
              <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={onSearch} />
            </div>
          </div>
        </div>
      </>
    );
  };

  const itemDisplayNameTemplate = (item: any) => {
    if (item?.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <img src={LoadingIcon} alt="search-icon" className=" h-7 w-7" />
          <Text className="text-primary-labelColor mt-2" label="Loading..." />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
        <Text className="text-secondary-gray-500 text-xxs" label={item.code} />
        {/* <Text className="text-secondary-gray-500 text-xxs" label={item.email} /> */}
      </div>
    );
  };

  const itemTemplate = (item: any) => {
    if (item.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <img src={LoadingIcon} alt="search-icon" className=" h-7 w-7" />
          <Text className="text-primary-labelColor mt-2" label="Loading..." />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const renderDisplayName = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">{rowData?.name}</div>
      </>
    );
  };

  const renderHeaderDisplayName = () => {
    return (
      <div className="flex">
        <p className="mr-1">Display Name</p>
        <img
          src={
            displayNameAsc == undefined
              ? sortIcon
              : displayNameAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortDisplayName}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderSystemCode = () => {
    return (
      <div className="flex">
        <p className="mr-1">System Code</p>
        <img
          src={codeAsc == undefined ? sortIcon : codeAsc == true ? sortIconAsc : sortIconsDesc}
          alt="Sort Icon"
          onClick={sortSystemCode}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderDescription = () => {
    return (
      <div className="flex">
        <p className="mr-1">Description</p>
        <img
          src={
            descriptionAsc == undefined
              ? sortIcon
              : descriptionAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortDescription}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderCreatedOn = () => {
    return (
      <div className="flex">
        <p className="mr-1">Created On</p>
        <img
          src={
            createdAtAsc == undefined
              ? sortIcon
              : createdAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortCreatedOn}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderCreatedBy = () => {
    return (
      <div className="flex">
        <p className="mr-1">Created By</p>
        <img
          src={
            createdByAsc == undefined
              ? sortIcon
              : createdByAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortCreatedBy}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderUpdatedOn = () => {
    return (
      <div className="flex">
        <p className="mr-1">Updated On</p>
        <img
          src={
            updatedAtAsc == undefined
              ? sortIcon
              : updatedAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortUpdatedOn}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderUpdatedBy = () => {
    return (
      <div className="flex">
        <p className="mr-1">Updated By</p>
        <img
          src={
            updatedByAsc == undefined
              ? sortIcon
              : updatedByAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortUpdatedBy}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderIsActive = () => {
    return (
      <div className="flex">
        <p className="mr-1">Is Active</p>
        <img
          src={
            isActiveAtAsc == undefined
              ? sortIcon
              : isActiveAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortIsActive}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderSystemCode = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">{rowData?.code}</div>
      </>
    );
  };

  const renderDescription = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">
          {rowData?.description}
        </div>
      </>
    );
  };

  const renderCreatedOn = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">
          {`${formattedDateLong(rowData?.createdAt)}`}
        </div>
      </>
    );
  };

  const renderCreatedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">
          {rowData?.createdByUserName}
        </div>
      </>
    );
  };

  const renderUpdatedOn = (rowData: any) => {
    return (
      <div className="text-primary-ptext-900 font-BrownLight text-left">
        {`${formattedDateLong(rowData?.updatedAt)}`}
      </div>
    );
  };

  const renderUpdatedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left">
          {rowData?.updatedByUserName}
        </div>
      </>
    );
  };

  const renderIsActive = (rowData: any) => {
    return (
      <>
        {rowData?.isActive ? (
          <div className="flex items-center ">
            <div className="w-2 h-2 rounded-full bg-primary-primaryBlue border-none"></div>
            <div className="ml-2 ">YES</div>
          </div>
        ) : (
          <div className="flex items-center ">
            <div className="w-2 h-2 rounded-full bg-secondary-gray-300 border-none"></div>
            <div className="ml-2 ">NO</div>
          </div>
        )}
      </>
    );
  };

  const renderActions = (rowData: any) => {
    return (
      <>
        <div className="flex items-center ">
          <div className="w-4 h-4 flex items-center">
            <img
              src={eyeIcon}
              alt="Sahayya Logo"
              className="cursor-pointer"
              onClick={(e: any) => {
                props.navigate(
                  `${linkStateCreateViewEdit}?mode=${PAGE_MODE.VIEW}&uid=${rowData?.uid}`
                );
              }}
            />
          </div>
          <div className="ml ">
            <ButtonBox
              buttonType={BUTTON_TYPE.MENU}
              onClick={(e) => {
                onToggleMenu(rowData, e);
              }}
              className="!rounded-none bg-transparent !hover:bg-primary-gray-300"
            />
          </div>
        </div>
      </>
    );
  };

  return (
    <>
      <div className="flex flex-col gap-3">
        <div className="flex w-full justify-between items-center">
          <Text label="heading.state" className="text-primary-ptext-1000 text-h2 font-bold flex" />
          <div className="flex gap-10">
            <ButtonBox buttonType={BUTTON_TYPE.FILTER_OUTLINE} onClick={onToggleFilter} />
            <ButtonBox
              buttonType={BUTTON_TYPE.CREATE}
              className="create-button"
              onClick={onCreate}
            />
          </div>
        </div>
        {showFilter && renderFilter()}

        <ReadOnlyTable
          standardDataFormatPromise={StateService.getAll}
          dataKey="id"
          search={dataTable.search}
          ref={dataTableRef}
        >
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderDisplayName}
            body={renderDisplayName}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderSystemCode}
            body={renderSystemCode}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs w-1/12  p-1 h-12 pl-6"
            header={renderHeaderDescription}
            body={renderDescription}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header={renderHeaderIsActive}
            body={renderIsActive}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderCreatedOn}
            body={renderCreatedOn}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderCreatedBy}
            body={renderCreatedBy}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderUpdatedOn}
            body={renderUpdatedOn}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderUpdatedBy}
            body={renderUpdatedBy}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header="Actions"
            body={renderActions}
          />
        </ReadOnlyTable>
      </div>
      <DialogBox
        onHide={
          isActivateClicked
            ? () => setShowDialogActivate(false)
            : () => setShowDialogDeactivate(false)
        }
        visible={isActivateClicked ? showDialogActivate : showDialogDeactivate}
        modal
        closable={false}
        dismissableMask={true}
      >
        <Text
          label={
            isActivateClicked ? "dialog.staticMaster.activate" : "dialog.staticMaster.deactivate"
          }
          className="text-primary-ptext-1000 text-base font-Roboto font-bold flex items-center justify-center"
        />
        <Text
          label={
            isActivateClicked
              ? "dialog.staticMaster.message.activate"
              : "dialog.staticMaster.message"
          }
          className="text-primary-ptext-1000 text-xs font-Roboto flex mt text-center justify-center mx-2 mt-4"
        />
        <div className="flex justify-center mt-4">
          <div className="mr-3">
            <ButtonBox
              buttonType={BUTTON_TYPE.CANCEL_OUTLINE}
              onClick={
                isActivateClicked
                  ? () => setShowDialogActivate(false)
                  : () => setShowDialogDeactivate(false)
              }
            />
          </div>
          <div className="mr-3">
            <ButtonBox
              buttonType={BUTTON_TYPE.SAVE_OUTLINE}
              onClick={isActivateClicked ? () => onSaveActivate() : () => onSaveDeactivate()}
            />
          </div>
          <div className="">
            <ButtonBox
              buttonType={BUTTON_TYPE.SEND_FOR_APPROVAL}
              className="approve-primary"
              onClick={isActivateClicked ? () => onApproveActivate() : () => onApproveDeActivate()}
            />
          </div>
        </div>
      </DialogBox>
      <MenuBox
        className="w-20 bg-white font-Roboto text-xs text-primary-pText-1300 pt-2 rounded-lg mt-0.5 flex justify-center align-center shadow-autoCompletePanel"
        model={editModuleCardMenu}
        ref={activeRowRef}
      />
      <div className="">
        <ToastCustom
          position="bottom-right"
          ref={toastMessageRef}
          toastText={toastMessage}
          variant={toastMessageVariant}
        />
      </div>
    </>
  );
}

export default withRouter(PageState);
